#gameboard {
    /* position: absolute;
    top:0;
	bottom: 0;
	left: 0;
    right: 0; */

    /* position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(0,-50%);
    -ms-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translate(-50%,-50%); */


    width: 500px;
    height: 500px;
    margin: auto;
    /* border: 3px solid; */
    font-family: 'Roboto', sans-serif;
    font-size: 2em;
}

table {
    text-align: center;
    /* margin: 0; */
    border-collapse: collapse;
    /* border: 1px solid black; */
    width: 100%;
    height: 100%;
    table-layout: fixed;
}

tr:nth-child(3), tr:nth-child(6){
    border-bottom: 3px solid;
}

td:nth-child(3), td:nth-child(6){
    border-right: 3px solid;
}

td {
    border: 1px solid;
    
    /* padding: 3px; */
    /* position: relative; */
    padding: 0px;
    margin: 0;
    width: auto;
    height: auto;
    text-align: center;
    vertical-align: middle;
}

.cell{
    border: 4px solid rgba(0,0,0,0);
    /* border-radius: 5px; */
    border-collapse: collapse;
    padding: 4px 0;
    height: 38px;
}

.active{
    border-color: lightblue;
}

.inactive{
    background-color: lightgrey;
}

.conflict{
    color: red;
}

#inputOptions{
    text-align: center;
}

.inputNum {
    width: 40px;
    height: 36px;
    /* padding: 5px; */
    font-size: .9em;
    margin: 10px 3px;
    display: inline-block;
    border: 4px solid lightblue;
    border-radius: 5px;
    text-align: center;
    padding: 2px 0;
}

.inputNum:hover{
    cursor: pointer;
}

.entered{
    background-color: lightblue;
}
